.primaryOuntlined {
    width: 88%;   /* 모바일 비율 */
    height: 52px;       /* 세로 고정 */
    padding: 0;
    border-radius: 10px;
    background-color: white;
    color: #1D2087;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 400;
    font-size: 16px;
    border: 2px solid #1D2087;
    cursor: pointer;
    transition: all 0.5s ease;
  }

@media (min-width: 412px) {
  .primaryOuntlined {
    width: 364px;  /* PC 이상에서는 고정 */
  }
}
  
  /* 클릭 시 0.5초 hover 느낌 */
.primaryOuntlined.clicked {
    background-color: #2A59FF;
    color: white;
    border: 2px solid white;
}
  
  /* 클릭 후 활성화 상태 */
.primaryOuntlined.activated {
    background-color: white;
    color: #8FA8FF;
    border: 2px solid #8FA8FF;
}
  
  /* 비활성화 상태 */
.primaryOuntlined.disabled {
    background-color: white;
    color: #505771;
    border: #505771 2px;
    cursor: not-allowed;
}